@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;600&family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.screenbox {
  height: 100vh;
  width: 60vw;
  min-width: 800px;
  margin: auto;
}

.hero-text {
  position: absolute;
  width: 40vw;
  min-width: 700px;
  font-family: "Crimson Text", serif;
  font-size: 80pt;
  text-align: center;
  font-weight: bold;
  line-height: 100px;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero-text span {
  color: #FFA5AB;
}

.navbox {
  width: 100vw;
  height: 40vh;
  flex-direction: row;
  text-align: center;
  display: block;
  position: fixed;
}

.navbox a img {
  display: inline-block;
  height: 100%;
  margin-left: 20px;
  margin-right: 20px;
}

.arrow-down {
  position: absolute;
  margin-top: 55vh;
  width: 0; 
  height: 0; 
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 15px solid black;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content-box {
  height: 60%;
  margin: auto;
  margin-top: 20%;
}

.subtitle {
  font-weight: 400;
  font-family: "Crimson Text", serif;
  font-size: 50pt;
  width: 100%;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
}

.about-me-text {
  width: 40%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 300;
  font-size: 20px;
  margin-left: 15%;
}

.about-me-text a {
  color: #FFA5AB;
  text-decoration: none;
}

.about-me-text a:hover {
  text-decoration: underline;
}

.random-image {
  position: absolute;
  width: 20%;
  margin-left: 50vw;
  margin-top: -200px;
  transform: rotate(199deg);
  animation: bob 5s 1s 1;
  animation-iteration-count:infinite;
}

/* Animation Keyframes*/
@keyframes bob {
  0% { margin-top: -220px }
  50% { margin-top: -180px }
  100% { margin-top: -220px }
}

.work-text {
  width: 70%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 300;
  font-size: 20px;
  margin: auto;
}

.work-row {
  margin-bottom: 15px;
  color: black;
  transition: 0.3s ease;
}

.work-row:hover {
  color: #FFA5AB;
}

.work-row span {
  float: right;
}

.contact-link {
  width: 100%;
  text-align: center;
}
.about-name {
    width: 40vw;
    min-width: 800px;
    font-size: 50pt;
    text-align: center;
    font-weight: bold;
    line-height: 100px;
    margin: auto;
    margin-top: 10vh;
  }
  
  .about-name span {
    color: #FFA5AB;
    transition: 0.5s ease;
  }

  .about-name span:hover {
    letter-spacing: 20px;
  }

  .about-body {
    width: 50vw;
    min-width: 700px;
    margin: auto;
    margin-top: 10vh;
    font-size: 16pt;
    text-align: center;
  }

  .about-body span {
    color: #FFA5AB;
    text-decoration: none;
    font-weight: bold;
    transition: 0.5s ease;
  }

  .about-body span:hover {
    text-decoration: underline;
  }

  .face {
    position: absolute;
    margin-top: -200px;
    height: 300px;
    width: auto;
    transform: rotate(90deg);
  }
